import React from 'react';
import { marked } from 'marked';
import PropTypes from 'prop-types';
import JsxParser from 'react-jsx-parser';

export const MarkdownJSX = ({ value, ...props }) => {
    const parser = (str) => {
        let plain = str;
        plain = String(plain).replace(/\n/g, '<br />').replace(/\r/g, '<br />');

        let markdown = marked.parse(plain);
        markdown = String(markdown).replace(/<p>(.+)\<\/p>/gim, '$1');

        return markdown;
    };

    return <JsxParser jsx={parser(value)} renderInWrapper={false} {...props} />;
};

MarkdownJSX.propTypes = {
    value: PropTypes.string.isRequired,
};

MarkdownJSX.defaultProps = {
    sanitize: true,
};
